import fetch from '@/utils/fetch'

export function searchProvince () {
  return fetch({
    methods: 'get',
    url: 'https://www.easy-mock.com/mock/59f1b06e4943e64bd135fdfb/witparking-op/monitor/searchPark_province'
  })
}

export function getRegionList (regionId, level) {
  return fetch({
    methods: 'get',
    url: 'https://www.easy-mock.com/mock/59f1b06e4943e64bd135fdfb/witparking-op/monitor/getregionsbyid',
    params: {
      regionId,
      level
    }
  })
}



