
<template>
  <div class="realtime">
    <el-tabs type="card">
      <el-tab-pane>
        <span slot="label">区域监控
          <i class="el-icon-date"></i>
        </span>
      </el-tab-pane>
      <el-tab-pane>
        <span slot="label">停车场监控
          <i class="el-icon-date"></i>
        </span>
      </el-tab-pane>
      <el-tab-pane>
        <span slot="label">违章监控
          <i class="el-icon-date"></i>
        </span>
      </el-tab-pane>
      <el-tab-pane>
        <span slot="label">停车诱导
          <i class="el-icon-date"></i>
        </span>
      </el-tab-pane>
    </el-tabs>
    <baidu-map class="map" :center="center" :zoom="zoom" @ready="handler" :style="{height:mapHeight+'px'}">
      <bm-city-list class="cascader" anchor="BMAP_ANCHOR_TOP_RIGHT" :offset="{top:'30px',right:'30px'}"></bm-city-list>
      <bm-boundary v-for="(val,key) in provinceList" :key="val.regionId" @click="getAreaNews" :name="val.regionName" :strokeWeight="1" strokeColor="#000000" :fillColor="colors[key]" :fillOpacity="0.5" :strokeOpacity="0.5"></bm-boundary>
      <!-- <bm-label v-for="(val,key) in cityList" :key="key" content="我爱北京天安门" :position="{val.longitude, val.latitude}" :labelStyle="{color: 'red', fontSize : '24px'}" title="Hover me"/> -->
    </baidu-map>
  </div>
</template>
<script>
  import { searchProvince, getRegionList } from '@/api/monitor/realtime'
export default {
    name: 'realtime',
    data () {
      return {
        center: { lng: 0, lat: 0 },
        zoom: 5,
        mapHeight: '',
        colors: [
          '#FBC5DC',
          '#FBC5DC',
          '#DBEDC7',
          '#E7CCAF',
          '#DBEDC7',
          '#E7CCAF',
          '#FEFCBF',
          '#E7CCAF',
          '#C8C1E3',
          '#FBC5DC',
          '#C8C1E3',
          '#E7CCAF',
          '#DBECC8',
          '#DBECC8',
          '#FCFBBB',
          '#FCFBBB',
          '#FCFBBB',
          '#FCFBBB',
          '#FCFBBB',
          '#E7CCAF',
          '#E7CCAF',
          '#E7CCAF',
          '#FBC5DC',
          '#FBC5DC',
          '#DBEDC7',
          '#DBEDC7',
          '#C8C1E3',
          '#C8C1E3',
          '#FBC5DC',
          '#FBC5DC',
          '#C8C1E3',
          '#FCFBBB',
          '#C8C1E3',
          '#C8C1E3'
        ],
        provinceList: []
      }
    },
    created () {
      this.getProvinceNews()
    },
    mounted () {
      // 绑定onresize事件 监听屏幕变化设置宽高
      this.$nextTick(() => {
        this.mapHeight = document.body.clientHeight - 200
        // areaClick(
      })
      window.onresize = () => {
        return (() => {
          window.mapHeight = document.body.clientHeight - 200
          this.mapHeight = window.mapHeight
        })()
      }
    },
    methods: {
      handler ({ BMap, map }) {
        this.center.lng = 116.404
        this.center.lat = 39.915
        this.zoom = 5
      },
      // 获取省份信息
      async getProvinceNews () {
        let res = await searchProvince()
        this.provinceList = res.returnObject
      },
      async getAreaNews ({ type, target, point, pixel }) {
        const map = target.map
        this.center = point
        this.zoom = 9
        map.clearOverlays()
        const res = await getRegionList()
        const list = res.returnObject.list
        this.cityList = list
        let pointArray = []
        list.map(v => {
          setTimeout(() => {
            const point = new BMap.Point(v.longitude, v.latitude)
            pointArray.push(point)
            const opts = {
              position: point
            }
            let label = new BMap.Label(v.name, opts) // 创建文本标注对象
            label.setStyle({
              color: '#ffffff',
              fontSize: '12px',
              lineHeight: '20px',
              fontFamily: '微软雅黑',
              backgroundColor: '#009fe9',
              boxShadow: '2px 1px 7px #344b75',
              borderRadius: '3px',
              border: 'none',
              borderLeft: '5px solid #ffffff',
              padding: '3px 10px'
            })
            label.addEventListener('click', this.addPane)
            map.addOverlay(label)
          }, 300)
        })
        // 让所有点在视野范围内
        map.setViewport(pointArray)
      },
      // 判断含此覆盖物
      hasOverLay (point, list) {
        return list.filter(v => v._point.lat === point.lat && v._point.lng === point.lng)
      },
      // 自定义覆盖物类
      addPane (e) {
        var p = e.currentTarget
        const map = p.map
        // 若以初始化覆盖物则只控制显影
        const s = map.Be && this.hasOverLay(p.point, map.Be) // 是否以初始化覆盖物
        if (s.length > 0 && s[0]._div.style.display === 'block') {
          s[0]._div.style.display = 'none'
          return false
        } else if (s.length > 0) {
          s[0]._div.style.display = 'block'
          return false
        }
        // 复杂的自定义覆盖物
        function ComplexCustomOverlay (point) {
          this._point = point
        }
        ComplexCustomOverlay.prototype = new BMap.Overlay()
        // 初始化覆盖物
        ComplexCustomOverlay.prototype.initialize = function (map) {
          this._map = map
          var div = this._div = document.createElement('div')
          var area = this._area = document.createElement('div')
          var sa = this._sa = document.createElement('span')
          var sd = this._sd = document.createElement('span')
          div.style.position = 'absolute'
          div.style.display = 'block'
          div.style.zIndex = BMap.Overlay.getZIndex(this._point.lat)
          area.className = 'op-area areaDetail-color'
          sa.textContent = '查看区域'
          sa.style.color = '#009FE9'
          sd.className = 'ml60'
          sd.innerHTML = '详情&nbsp&nbsp<i class="iconfont areaDetail"></i>'
          const tmpl = `<div class="parking-areaDetail-box areaDetail-color">
                          <div class="parking-areaDetail clearfix">
                            <div class="areaDetail-detail">
                              <ul>
                                <li>车场数:<span class="numberYard">0</span></li>
                                <li>总泊位数:<span class="numberBerths">0</span></li>
                                <li>占用泊位数:<span class="occupiedBerths">0</span></li>
                                <li>余位数:<span class="digitsBerth">0</span></li>
                                <li>巡检人数:<span class="inspectionPerson">0</span></li>
                                <li>稽查人数:<span class="auditingPerson">0</span></li>
                                <li>监控数量:<span class="monitorNumber">0</span></li>
                              </ul>
                            </div>
                          </div>
                        </div>`
          area.appendChild(sa)
          area.appendChild(sd)
          div.innerHTML = tmpl
          div.appendChild(area)
          map.getPanes().labelPane.appendChild(div)
          sa.addEventListener('click', function () {
            alert(123)
          })
          sd.addEventListener('click', function () {
            let box = this._div = document.createElement('div')
            const m = `
                  <div class="parkingDetail-show" style="display: block;"><div class="total-berth"><p>收入:<span>￥</span><span class="totalBerth">0</span></p></div><div class="extr-berth"><div class="free-berth">月交通静态吸引量：<span class="freeBerth">0</span></div></div><div class="change"><div class="change-l"><div class="month"><strong class="monthUseRate">0.00%</strong><p>月利用率</p></div><div class="days"><strong class="rs dayUseRate">0.00%</strong><p>日利用率</p></div></div><div class="change-r"><div class="month"><strong class="monthTurnOverRate">0.00</strong><p>月周转率</p></div><div class="days"><strong class="rs dayTurnOverRate">0.00</strong><p>日周转率</p></div></div><div class="clear"></div></div></div>
            `
            box.innerHTML = m
            area.appendChild(box)
          })
          return div
        }
        ComplexCustomOverlay.prototype.draw = function () {
          var map = this._map
          var pixel = map.pointToOverlayPixel(this._point)
          this._div.style.left = pixel.x + 'px'
          this._div.style.top = pixel.y + 'px'
        }
        var myCompOverlay = new ComplexCustomOverlay(new BMap.Point(p.getPosition().lng, p.getPosition().lat))
        map.addOverlay(myCompOverlay)
      }
    }
}
</script>
<style lang="less" scoped>
 
.realtime {
  padding-top:20;
  .map {
    width: 100%;
    .cascader {
      width: 300;
    }
  }
}
</style>


